<template>
  <div>
    <!-- Header -->
    <notifications></notifications>
    <img
      style="width: 50%; position: absolute; top: 0; left: 0"
      src="/img/brand/tk_login_pic.png"
    />
    <!-- Page content -->
    <div class="container" style="position: relative; z-index: 1">
      <div class="row justify-content-center">
        <div class="tk-bg-color-white tk-px-margin-top-px100 tk-text-center">
          <img src="/img/brand/tk_logo_icon.png" class="tk-px-width-px90" />
          <fade-transition>
            <div
              class="tk-font-color-orange tk-px-margin-top-px30 text-lg"
              v-show="failed"
            >
              <span class="tk-opacity-0">0</span>
              <span class="text-sm">{{ failedMsg }}</span>
              <span class="tk-opacity-0">0</span>
            </div>
          </fade-transition>
          <fade-transition>
            <div
              class="tk-font-color-lightGray tk-px-margin-top-px30 text-lg"
              v-show="!failed"
            >
              <span>Console</span>
            </div>
          </fade-transition>
        </div>
      </div>
      <div class="row justify-content-center">
        <div class="col-lg-5 col-md-7 tk-px-margin-top-px30">
          <validation-observer ref="formValidator">
            <form role="form" style="width: 335px; margin: 0 auto">
              <!-- account -->
              <template>
                <base-input
                  alternative
                  name="Email"
                  prepend-icon="fas fa-user"
                  placeholder="Your account"
                  v-model="model.email"
                  inputClasses="tk-px-height-px64"
                  inputGroupClasses="tk-border-color-gray-200"
                  id="loginAccount"
                ></base-input>

                <base-input
                  alternative
                  name="Password"
                  :rules="{ required: false }"
                  prepend-icon="ni ni-lock-circle-open"
                  type="password"
                  placeholder="Password"
                  v-model="model.password"
                  inputClasses="tk-px-height-px64"
                  inputGroupClasses="tk-border-color-gray-200"
                  class="tk-form-input-pwd"
                >
                </base-input>

                <div class="text-center">
                  <button-with-loading
                    style="margin: 30px auto 1rem"
                    text="LOGIN"
                    @start="login"
                    width="180"
                    :type="invalidEmail ? 'disable' : 'main'"
                    :complete="isButtonLoadingComplete"
                  ></button-with-loading>
                </div>
              </template>
            </form>
          </validation-observer>
        </div>
      </div>
    </div>

    <alert
      :title="this.alert.title"
      :content="this.alert.content"
      :left="this.alert.buttons.left.title"
      :right="this.alert.buttons.right.title"
      v-if="alert.isShow"
      @onLeftTapped="this.alert.buttons.left.action"
      @onRightTapped="this.alert.buttons.right.action"
    >
    </alert>
  </div>
</template>

<script>
/* eslint-disable */
import $ from "jquery";
import bus from "@/bus.js";
import { FadeTransition } from "vue2-transitions";
import collections from "@/services/collections";
import queryCollection from "@/services/query";

export default {
  components: {
    avatar: () => import("@/tkViews/components/layout/Avatar"),
    buttonWithLoading: () => import("../components/layout/ButtonWithLoading"),
    FadeTransition,
    alert: () => import("../components/Modal/Alert"),
  },
  data() {
    return {
      model: {
        fullName: "",
        email: "",
        password: "",
      },
      invalidEmail: true,
      invalidPwd: true,
      loading: false,
      isButtonLoadingComplete: false,
      failed: false,
      failedMsg: "",
      alert: {
        isShow: false,
        title: "",
        content: "",
        buttons: {
          left: {
            title: "",
            action: () => {},
          },
          right: {
            title: "",
            action: () => {},
          },
        },
      },

      accountManageement: []
    };
  },
  async created() {
    let user = localStorage.getItem("adminUser");
    let accountManageement = await queryCollection.getAccountManagement()
    let info = await collections.config.doc("salesAccount").get();
    if (!this.$tools.isNull(user)) {
      user = JSON.parse(user);
      if (
        info.data().account === user.account &&
        info.data().password === user.password
      ) {
        this.$router.push({ name: "DynamicLink" });
      }
    }

    let query = this.$route.query;
    console.log(query)
    if (!this.$tools.isNull(query.account)) {
      this.model.email = query.account;
    }
  },
  watch: {
    failed(newVal) {
      if (newVal) {
        setTimeout(() => {
          this.failed = false;
          this.failedMsg = "";
        }, 1000);
      }
    },
    model: {
      handler(newVal, oldVal) {
        console.log(newVal)
        if (!this.loading) {
          if (newVal.email.length > 0 && newVal.password.length > 6) {
            this.invalidEmail = false;
          } else {
            this.invalidEmail = true;
          }

          if (newVal.password.length === 6) {
            this.invalidPwd = false;
          } else {
            this.invalidPwd = true;
          }
        }
      },
      deep: true,
    },
  },
  methods: {
    login() {
      let self = this;
      self.isButtonLoadingComplete = false;
      if (!self.$tools.isNull(self.model.email)) {
        self.loading = true;

        collections.config
          .doc("salesAccount")
          .get()
          .then((doc) => {
            if (doc.exists) {
              let sales = doc.data().sales;
              for (let i = 0; i < sales.length; i++) {
                console.log(sales[i].account, self.model.email)
                if (sales[i].account == self.model.email) {
                  if (sales[i].password == self.model.password) {
                    let user = sales[i];
                    // console.log("登陆成功：", user);

                    localStorage.setItem("adminUser", JSON.stringify(user));

                    self.loading = false;
                    self.isButtonLoadingComplete = true;

                    self.$router.push({ name: "DynamicLink" });
                  } else {
                    console.error("登陆失败：密码错误");
                    self.isButtonLoadingComplete = true;

                    self.$notify({
                      message:
                        "Login Failed, please check your password and try again!",
                      type: "error",
                    });
                  }
                  break;
                } else {
                  if (i === sales.length - 1) {
                    console.error("登陆失败：账号错误");
                    self.isButtonLoadingComplete = true;

                    self.$notify({
                      message:
                        "Login Failed, please check your account and try again!",
                      type: "error",
                    });
                  }
                }
              }
            }
          })
          .catch((error) => {
            console.error("登陆失败：", error);
            self.isButtonLoadingComplete = true;

            self.$notify({
              message: "Login Failed, please try again!",
              type: "error",
            });
          });
      }
    },
  },
};
</script>
